body {
  margin: 0;
  font-family: "roboto", Arial, Helvetica, sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #d5d4a9;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container{
  max-width: 700px;
  margin: 50px auto;
 
}

.search-paginate{
  max-width: 280px;
  margin: 0 auto;
}
.weather{
    width: 350px;
    border-radius: 6px;
    box-shadow: 10px -2px 20px 2px rgb(0 0 0/40%);
    color: white;
    background-color: #333 ;
    margin: 50px auto 0 auto;
    padding: 0px 20px 20px 20px;
}

.top, .bottom{
    display: flex;
    justify-content: space-between ;
    align-items: center ;
}

.city{
    font-weight: 600; 
    font-size: 18px;
    line-height: 1;
    margin: 0;
    letter-spacing: 1px;
}

.weather-description{
    font-weight: 400;
    font-size: 14px;
    line-height: 1;
    margin: 0;

}

.weather-icon{
    width: 100px;
}

.temperature{
    font-weight: 600px;
    font-size: 60px;
    width: auto;
    letter-spacing: -5px;
    margin: 10px 0;

}

.details{
    width: 50%;
    padding:0 20px;
    
}

.parameter-row{
    display: flex;
    justify-content: space-between;
    margin-top: 5px;
}
.parameter-label{
    text-align: left;
    font-weight: 400;
    font-size: 12px;
}

.parameter-value{
    font-weight: 600;
    text-align: right;
    font-size: 12px;
}


.title{

    font-size: 22px;
    font-weight: 700;
    margin-top: 50px;
   

}

.daily-item{
    background-color: #f5f5f5;
    display: flex;
    align-items: center;
    margin: 5px;
    height: 40px;
    border-radius: 15px;
    padding: 5px 20px;
    font-size: 14px;
   
}

.icon-small{
    width: 40px;
}

.day{
    color: #212121;
    flex: 1 1;
    font-weight: 600;
    margin-left: 15px;

}

.description{
   
    margin-right: 15px;
    text-align: right;
}

.min-max{
    color: #757575;
}
